<template>
  <div class="board">
      <b-form-group>
        <b-form-radio-group
          id="btn-radios-2"
          v-model="selected"
          :options="options"
          button-variant="outline-primary"
          size="lg"
          name="radio-btn-outline"
          buttons
        ></b-form-radio-group>
      </b-form-group>
      <span>
        <JsonCSV :data = "currencies" class="csv">
          <button class="btn btn-primary csv"><img src="../assets/img/download.png" class="downloadimg"/>Free CSV</button>
        </JsonCSV>
      </span>
      <span class="showrow">
        show rows:
        <select>
          <option>20</option>
          <option>50</option>
          <option>100</option>
        </select>
      </span>

    <table class="table row">
      <thead>
        <tr>
          <td>#</td>
          <td class="col-sm-4 col-xs-1">Name</td>
          <td class="col-sm-1">Price</td>
          <td class="col-sm-1">Rating</td>
          <td class="col-sm-1">24 Change%</td>
          <td class="col-sm-1">Market Cap</td>
          <td class="col-sm-1"># Of Trades</td>
          <td class="col-sm-1">% Of Trades</td>
          <td class="col-sm-1">Price Chart</td>
        </tr>
      </thead>
      <tbody>
        <CurrencyRow
          v-for="(item, i) in currencies"
          :key="item.symbol"
          :ticker="tickers[item.symbol] || {}"
          :info="item"
          :rank="i"
        ></CurrencyRow>
      </tbody>
    </table>
  </div>
</template>
<script>
import { mapState } from "vuex";
import CurrencyRow from "@/components/CurrencyRow";
import JsonCSV from 'vue-json-csv';

export default {
  created() {
    console.log("sdfsdfs");
    this.getNews();
    // this.interval = setInterval(
    //   function () {
    //     this.getNews();
    //   }.bind(this),
    //   120000
    // );
  },
  components: {
    CurrencyRow,
    JsonCSV,
  },
  beforeDestroy() {
    clearInterval(this.interval);
  },
  data() {
    return {
      news: [],
      interval: null,
      test: {},
      selected: "radio1",
      options: [
        { text: "All", value: "radio1" },
        { text: "Gainer", value: "radio2" },
        { text: "Loser", value: "radio3" },
        { text: "Dead", value: "radio4" },
      ],
      csv_data: [
        {a: 'a', b:'b'},
      ],
    };
  },
  methods: {
    truncateText(value) {
      if (value.length > 135) {
        const substr = value.substring(0, 135);
        return substr.substring(0, substr.lastIndexOf(" ")) + " ...";
      } else {
        return value;
      }
    },
    async getNews() {
      let url = "https://data.messari.io/api/v1/assets/btc/metrics/market-data";
      console.log(url);
      let response = await fetch(url);
      if (response.ok) {
        let json = await response.json();
        // this.test = json;
        this.news = json["Data"];
        console.log(json);
      } else {
        console.log("Fetch Error :-S", response.status);
      }
    },
  },
  filters: {
    tags: function (value) {
      if (!value) return "";
      return value.split("|").join(", ");
    },
  },
  computed: {
    ...mapState(["tickers", "currencies"]),
  },
};
</script>
